import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Helmet } from 'react-helmet';
import { Upload, FileUp } from 'lucide-react';

const SMTPTester = () => {
    // SMTP Configuration State
    const [smtpConfig, setSmtpConfig] = useState({
        server: '',
        port: '',
        username: '',
        password: '',
    });
    const [isConfigSaved, setIsConfigSaved] = useState(false);

    const [isDragging, setIsDragging] = useState(false);
    const [files, setFiles] = useState([]);
    const [sending, setSending] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [recipient, setRecipient] = useState('');
    const [subject, setSubject] = useState('');
    const [body, setBody] = useState('');
    const [emails, setEmails] = useState([]);

    // Handle Drag and Drop for Attachments
    const handleDragEnter = (e) => {
        e.preventDefault();
        setIsDragging(true);
    };

    const handleDragLeave = (e) => {
        e.preventDefault();
        setIsDragging(false);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleDrop = (e) => {
        e.preventDefault();
        setIsDragging(false);
        const droppedFiles = Array.from(e.dataTransfer.files);
        setFiles(prevFiles => [...prevFiles, ...droppedFiles]);
    };

    const handleFileChange = (e) => {
        setError('');
        setSuccess('');
        const selectedFiles = Array.from(e.target.files);
        setFiles(prevFiles => [...prevFiles, ...selectedFiles]);
    };

    // Handle Save SMTP Config
    const handleSaveSmtpConfig = () => {
        if (!smtpConfig.server || !smtpConfig.port || !smtpConfig.username || !smtpConfig.password) {
            setError('All SMTP fields must be filled.');
            return;
        }
        setIsConfigSaved(true);
        setSuccess('SMTP configuration saved successfully.');
    };

    // Handle Send Email
    const handleSendEmail = async () => {
        if (!recipient || !subject || !body || !isConfigSaved) {
            setError('All fields must be filled and SMTP configuration must be saved.');
            return;
        }

        setSending(true);
        setError('');
        setSuccess('');

        const formData = new FormData();
        formData.append('recipient', recipient);
        formData.append('subject', subject);
        formData.append('body', body);
        formData.append('smtpConfig', JSON.stringify(smtpConfig)); // Pass SMTP config as payload
        files.forEach(file => formData.append('attachments', file));

        try {
            await axios.post('/api/send-email', formData);
            setFiles([]);
            setRecipient('');
            setSubject('');
            setBody('');
            setSuccess('Email sent successfully!');
        } catch (err) {
            console.error('Error sending email:', err);
            setError('Failed to send email. Please try again.');
        } finally {
            setSending(false);
        }
    };

    return (
        <>
            <Helmet>
                <title>Free SMTP Tester</title>
                <meta name="description" content="Test SMTP configurations by sending temporary emails with attachments." />
            </Helmet>

            <main className="max-w-3/4 h-full flex flex-col items-center min-h-screen">
                <div className="container bg-white md:p-10 rounded-lg mx-auto grid grid-cols-1 md:grid-cols-12 gap-1">
                    {/* SMTP Configuration Form */}
                    <div className="max-w-2xl mx-auto mt-10 p-8 bg-gradient-to-br from-white-900 to-white-800 rounded-xl shadow-2xl md:col-span-3">
                        <h1 className="text-4xl font-bold text-center mb-4 text-gray-800">SMTP Configuration</h1>

                        {/* SMTP Fields */}
                        <input
                            type="text"
                            placeholder="SMTP Server"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={smtpConfig.server}
                            onChange={(e) => setSmtpConfig({ ...smtpConfig, server: e.target.value })}
                        />
                        <input
                            type="text"
                            placeholder="Port"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={smtpConfig.port}
                            onChange={(e) => setSmtpConfig({ ...smtpConfig, port: e.target.value })}
                        />
                        <input
                            type="text"
                            placeholder="Username"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={smtpConfig.username}
                            onChange={(e) => setSmtpConfig({ ...smtpConfig, username: e.target.value })}
                        />
                        <input
                            type="password"
                            placeholder="Password"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={smtpConfig.password}
                            onChange={(e) => setSmtpConfig({ ...smtpConfig, password: e.target.value })}
                        />

                        <button
                            onClick={handleSaveSmtpConfig}
                            className="bg-main-color hover:bg-blue-300 text-white font-bold py-3 px-6 rounded-lg cursor-pointer mt-4 inline-flex items-center"
                        >
                            Save SMTP Config
                        </button>
                    </div>

                    {/* Email Sending Section */}
                    <div className="max-w-2xl mx-auto mt-10 p-8 bg-gradient-to-br from-white-900 to-white-800 rounded-xl shadow-2xl md:col-span-9">
                        <h1 className="text-4xl font-bold text-center mb-4 text-gray-800">Send Email</h1>

                        <input
                            type="email"
                            placeholder="Recipient Email"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={recipient}
                            onChange={(e) => setRecipient(e.target.value)}
                        />
                        <input
                            type="text"
                            placeholder="Subject"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={subject}
                            onChange={(e) => setSubject(e.target.value)}
                        />
                        <textarea
                            placeholder="Email Body"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={body}
                            onChange={(e) => setBody(e.target.value)}
                        />

                        {/* Attachments Drag and Drop */}
                        <div
                            className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${isDragging ? 'border-blue-500 bg-blue-50' : 'border-gray-300'}`}
                            onDragEnter={handleDragEnter}
                            onDragLeave={handleDragLeave}
                            onDragOver={handleDragOver}
                            onDrop={handleDrop}
                        >
                            <input
                                type="file"
                                multiple
                                className="hidden"
                                id="file-input"
                                onChange={handleFileChange}
                            />
                            <label
                                htmlFor="file-input"
                                className="bg-gray-600 hover:bg-gray-300 text-white font-bold py-3 px-6 rounded-lg cursor-pointer w-auto inline-flex items-center"
                            >
                                <FileUp className="mr-2" size={20} />
                                Attach Files
                            </label>
                            <p className="mt-2 text-gray-500">or drag and drop files here</p>
                        </div>

                        {/* Error and Success Messages */}
                        {error && <p className="text-red-500 mt-4">{error}</p>}
                        {success && <p className="text-green-500 mt-4">{success}</p>}

                        {/* Send Button */}
                        <button
                            onClick={handleSendEmail}
                            disabled={sending}
                            className="bg-main-color hover:bg-blue-300 text-white font-bold py-3 px-6 rounded-lg cursor-pointer mt-4 inline-flex items-center"
                        >
                            {sending ? 'Sending...' : 'Send Email'}
                        </button>
                    </div>
                </div>
            </main>
        </>
    );
};

export default SMTPTester;
